@tailwind base;
@tailwind components;
@tailwind utilities;

*{
  /* outline: 1px greenyellow solid; */
}



body{
    background-color: #EDEEF0;
}


hr {
    border: none;
    height: 1px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Tailwind gray-300 */
    margin: 10px 0;
  }
  


  .scrollbar-hide::-webkit-scrollbar {
    display: none;
  }

  .lexend {
    font-family: "Lexend", serif;
    font-optical-sizing: auto;
    font-style: normal;
  }
  


  /* WebKit (Chrome, Safari) */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #888 #f1f1f1;
}
